.add_product_asd {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.308);
  backdrop-filter: blur(2px);
  transition: 0.4s ease;
  opacity: 0;
  display: none;
  justify-content: center;
  align-items: center;
}
.modal_add_product {
  position: relative;
  overflow: hidden;
  background: #f3f3f3;
  width: 55vw;
  height: 92vh;
  border-radius: 1vh;
  display: flex;
  justify-content: space-between;
  padding: 5vh 1.7vw 0 1.7vw;
  box-sizing: border-box;
  background: #f3f3f3;
}
.settign_up {
  font-family: Jura;
  font-size: 2.4vh;
  font-weight: 700;
  margin: 2vh 0 0 0;
}
.input_field {
  margin-top: 1.6vh;
  background: #fff;
  width: 24vw;
  height: 22vh;
  border-radius: 1vh;
}
.input {
  display: block;
  margin-top: 1.4vh;
  border: none;
  outline: none;
  box-shadow: inset 0px 0px 0.6vh 0px rgba(0,0,0,0.308);
  width: 24vw;
  height: 4.4vh;
  border-radius: 1vh;
  padding: 0 0 0 1vw;
  box-sizing: border-box;
  font-family: Jura;
  font-size: 2.4vh;
  font-weight: 500;
}
.close_img {
  width: 5vh;
  position: absolute;
  right: 1vh;
  top: 1vh;
  cursor: pointer;
}
.radio-buttons {
  margin-top: 2vh;
  display: flex;
  color: #fff;
  gap: 2vw;
}
.radio-button {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  cursor: pointer;
}
.radio-button input[type="radio"] {
  display: none;
}
.radio-circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #aaa;
  position: relative;
  margin-right: 10px;
}
.radio-circle::before {
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ddd;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: all 0.2s ease-in-out;
}
.radio-button input[type="radio"]:checked + .radio-circle::before {
  transform: translate(-50%, -50%) scale(1);
}
.radio-button input[type="radio"]:checked + .radio-circle::before {
  background-color: #28d075;
}
.radio-label {
  font-family: Jura;
  font-size: 15px;
  font-weight: 500;
  color: #000;
}
.modal_sep {
  width: 0.1vw;
  height: 85%;
  background: #9c9c9c;
}
.text_area {
  margin-top: 2vh;
  width: 24vw;
  height: 30vh;
  resize: none;
  font-family: Jura;
  font-size: 2.6vh;
  font-weight: 500;
  outline: none;
  border: none;
  box-shadow: inset 0px 0px 0.6vh 0px rgba(0,0,0,0.308);
  border-radius: 1vh;
  padding: 1vh 1vw;
  box-sizing: border-box;
}
.submit_button {
  margin-top: 4vh;
  background: #85d4a9;
  font-family: Jura;
  font-size: 2vh;
  font-weight: 700;
  border-radius: 0.5vh;
  width: 10vw;
  height: 6vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
